'use client';
import { useState, useEffect } from 'react';
import DrinkCard from './DrinkCard';
import singleDrinkLoader from '../utils/singleDrinkLoader';

interface SingleDrinkProps {
  drinkId: string;
}

const SingleDrink: React.FC<SingleDrinkProps> = ({ drinkId }) => {
  const [drink, setDrink] = useState<any>(null);

  useEffect(() => {
    const fetchDrinkDetails = async () => {
      const drink = await singleDrinkLoader(drinkId);
      setDrink(drink);
    };
    fetchDrinkDetails();
  }, [drinkId]);

  if (!drink) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <DrinkCard drink={drink} />
    </div>
  );
};

export default SingleDrink;
